import axios from "axios"
import { cartTracking, clientHost } from "./api"

export const fetchShopifyProduct = (productName) => {
    return axios.get(`${productName}.js`)
}
export const addToCart = (payload) => {
    return axios.post(`${clientHost}/cart/add.js`, payload)
}
export const getCartItemCount = () => {
    return axios.get(`${clientHost}/cart.js`)
}
export const trackAddToCart = (obj) => {
    axios.post(cartTracking, obj).then(res => { }).catch(error => console.log("error", error))
}