import React, { Component } from 'react'
import { getCartItemCount } from '../../../actions/cartActions'
import { clientHost } from '../../../actions/api'
import { NO_PRODUCT_IMAGE } from '../../../constants'

class ContinueShop extends Component {
    state = { cartItems: 1 }

    componentDidMount() {
        getCartItemCount().then(res => {
            const { item_count } = res.data
            if (item_count) this.setState({ cartItems: item_count })
        }).catch(err => {

        })
    }

    render() {
        const { productDetails, closeAddtocart, productImg, tagshopProductDeatil, onBack } = this.props
        return <div className='tb_cart_container'>
            <div className="tb_cart_back_btn" onClick={onBack}>
                <div className='tb__icon tb-arrow-left'><div></div></div>
                Back
            </div>
            <div className="tb_cart_details">
                <div className="tb_cart_p_img">
                    <img src={productDetails.featured_image ? productDetails.featured_image.src : productImg} alt="" onError={e => e.target.src = NO_PRODUCT_IMAGE}  height={400} width={300} />
                </div>
                <div className='tb_cart_p_details'>
                    <div className='tb_card_de_in'>
                        <div className="tb_cart_p_title">{productDetails.name}</div>
                        <div className="tb_cart_p_price">{tagshopProductDeatil?.UgcProduct?.price_currency_symbol}{tagshopProductDeatil?.UgcProduct.product_discount > 0 ? tagshopProductDeatil?.UgcProduct.product_discount : tagshopProductDeatil?.UgcProduct.product_price}</div>
                        {this.state.cartItems ? <div className="tb_cart_p_total">
                            Your cart now has {this.state.cartItems} item
                        </div> : <></>}
                    </div>
                    <div className="tb_cart_actions tb_cart_c_action">
                        <div className="tb_cart_primary" onClick={() => window.location = `${clientHost}/cart`}>Proceed to checkout</div>
                        <div className="tb_cart_secondary" onClick={closeAddtocart}>Continue Shopping</div>
                    </div>
                </div>
            </div>
        </div>
    }
}
export default ContinueShop